body, html {
    height: 100%;
    min-height: 100%;
}
.cart-container {
    width: 100%;
    margin: 0;
    padding: 0;

}
* {
    box-sizing: border-box;
}
.cart-table {
    display: grid;
    grid-template-columns: max-content minmax(10em, 20em) repeat(3, max-content);
    row-gap: 1em;
    width: 100%;
    font-family: Inter;
    margin: 20px;
    background-color: transparent;
    padding-top: 20px;
    /*padding-bottom: 250px;*/
    color: black;
    overflow-y: auto;
    max-height: calc(100vh - 300px);  /* Adjust the max-height based on your header height */
}
.cart-container {
    width: 100vw;
    height: 100vh;
}
.cart2{
    display: contents;
}

.cart2 li {
    display: contents;
}


/*.table-heading {*/
/*    background-color: var(--secondary-color);*/
/*    color: white;*/
/*}*/
.table-heading {
    background-color: var(--primary-color);
    color: white;
    font-weight: bolder;
    display: grid;
    font-size: large;
    grid-template-columns: 1fr 1fr 1fr 1fr;  /* Adjust the number of columns based on your requirements */
    grid-column-gap: 1px;  /* Adjust the gap between columns if needed */
}
.heading-book,
.heading-price,
.heading-quantity,
.heading-subtotal {
    background-color: var(--secondary-color);
    padding: 0.5em;
    text-align: center;
}

.li-book,
.li-price,
.li-quantity,
.li-subtotal {
    background-color: var(--secondary-color);
    padding: 0.5em;
    text-align: center;
}
.cart-book-image {
    display: flex;
    align-items: center;  /* Center the content vertically */
}

.cart-book-image img {
    display: block;
    width: 80px;
    height: auto;
    margin-right: 1em;  /* Add margin to separate the image from the heading */
}

.table-heading > * {
    background-color: var(--primary-color);
    padding: 0.5em;
}

