.cart-book-image {
    padding: 0 1em;
}

.cart-book-image > * {
    margin-left: auto;
    margin-right: 0;
}

.cart2 img {
    display: block;
    width: 100px;
    height: auto;
}

.rect {
    background-color: var(--primary-color);
}

.narrow-rect {
    width: 75px;
    height: 100px;
}

.square {
    width: 100px;
    height: 100px;
}

.wide-rect {
    width: 125px;
    height: 100px;
}

.cart-book-price {
    padding-left: 1em;
    text-align: center;
}

.cart-book-quantity {
    text-align: center;
    padding-left: 1em;
    padding-right: 1em;

}

.cart-book-subtotal {
    text-align: center;
    padding-left: 1em;
    padding-right: 1em;
}


.subtotal-section {
    padding-left: 10px;
    display: flex;
    justify-content: flex-end;
    margin: 5px;
}

.subtotal-label {
    font-weight: bold;
    color: var(--primary-color);
    margin: 0 10px 0 10px;
}

.subtotal-value {
    font-weight: bold;
    color: var(--primary-color);
    margin: 0 10px 0 10px;
}


.cart-book-title {
    font-weight: bold;
    font-size: large;
}
.icon-button {
    border: none;
    cursor: pointer;
    background-color: transparent;
    color: #000000;
}

.inc-button {
    font-size: 1.125rem;
    /*color: var(--primary-color);*/
    /*margin-right: 0.25em;*/

}

.inc-button:hover {
    color: var(--secondary-color);

}

.dec-button {
    font-size: 1.125rem;
    /*color: var(--primary-color);*/

}

.dec-button:hover {
    color: var(--secondary-color);
}


.row-line {
    border-bottom: 1px solid black;
}

