html {
    height: 100%;
    width: 100%;
}

.header-wrapper .header-wrapper__links span {
    position: absolute;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    right: -5px;
    top: -10px;
    background-color: red;
    color: white;
    border-radius: 50%;
    height: 20px;
    width: 20px;
    font-size: 14px;
}


.book-tile {
    padding: 10px;
    background: rgba(255, 255, 255, 0.50);
    border-radius: 10px;
    overflow: hidden;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    display: inline-flex;
}

.main .welcome-text-div {
    display: flex;
    justify-content: center;
    padding-top: 20px;
}

nav button {
    margin: 0;
    padding: 0;
    border: 0;
    background-color: rgba(255, 255, 255, 0.50);
    color: var(--secondary-text-color);

}

.book-tile-cat {
    padding: 10px;
    background: rgba(255, 255, 255, 0.50);
    border-radius: 10px;
    overflow: hidden;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    display: inline-flex;
    margin-left: 20px;
    margin-top: 20px;
}

.book-desc-div {
    display: flex;
    flex-direction: column;
    margin-right: 40px;
    margin-left: 40px;
    align-items: center;
    height: 100%;
    justify-content: space-between;

}

.book-desc-div h3 {
    margin: 0;
}

.book-desc-div p {
    margin: 0;
    font-style: italic;
}

.book-desc-div button {
    height: 30px;
    width: 130px;
    background-color: var(--primary-button--color);
    border-radius: 5px;
    border: 0;
    font-family: Inter;
    font-weight: 800;
    font-size: 16px;
    color: var(--primary-color);
    margin: 10px;
    cursor: pointer;
}

.book-desc-div button:hover {
    background-color: var(--tertiary-button-color);
}

.book-tile h4 {
    margin: 0;
    font-weight: 700;
    font-size: 16px;
    font-family: Inter;

}

.book-tile a {
  text-decoration: none;
    color: var(--primary-color);

}

.book-tile-cat {
    flex-direction: row;
}

.book-tile-cat .book-image {
    margin-left: 10px;
    cursor: pointer;
}

.book-image span {
    display: flex;
    height: 0;
    width: 0;
    position: relative;
    top: 6px;
    left: 94px;
}

.book-image span img {
    height: 30px;
    width: 30px;
}


.book-tiles-div {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    margin: 20px;

}

.tiles {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 100%;
    margin-left: 60px;
}

.tiles div {
    display: flex;
}

.sidenav {
    height: 100%;
    width: 0;
    position: fixed;
    z-index: 1;
    top: 0;
    left: 0;
    background-color: #111;
    overflow-x: hidden;
    transition: 0.5s;
    padding-top: 60px;
}

.sidenav a {
    padding: 8px 8px 8px 32px;
    text-decoration: none;
    font-size: 25px;
    color: #818181;
    display: block;
    transition: 0.3s;
}

.sidenav a:hover {
    color: #f1f1f1;
}

.sidenav .closebtn {
    position: absolute;
    top: 0;
    right: 25px;
    font-size: 36px;
    margin-left: 50px;
}

.book-tile p {
    margin: 0;
    font-weight: 400;
    font-size: 15px;
    font-family: Inter;
    font-style: italic;
}

.main .book-tiles {
    width: 100%;
    margin-top: 5px;

}

.book-image img {
    height: 200px;
    width: 125px;
}

.category-main,
.main {
    flex-grow: 1;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
}

.category-main {
    flex-direction: row;
}

.categories ul {
    position: fixed;
    z-index: 1;
    top: 0;
    bottom: 0;
    left: 0;
    margin: auto;
    height: 280px;
    width: 10%;
    padding: 0;
    text-align: center;
}


.main .category-title {
    font-size: 32px;
    /*padding-top: 20px;*/
    font-family: "Inter";
    color: var(--secondary-color);
    align-self: flex-start;
    margin-left: 92px;
}

.categories-div {
    margin-right: 50px;
    display: flex;
    height: 60vh;

}

.categories-div .categories {
    display: flex;
    flex-direction: column;

}

