.nav-button-selected,
.nav-button-unselected {
    cursor: pointer;
    font-family: Inter;
    font-size: 25px;
    width: 350px;
    margin: 5px 0 5px 0;
    padding: 5px;
    border-radius: 5px;
    transition: background-color 0.2s, color 0.2s; /* Add a smooth transition */
}
.nav-button-unselected:hover {
    background-color: var(--secondary-color);
    color: var(--primary-color);
}

.nav-button-unselected {
    background-color: var(--primary-color);
    color: var(--secondary-color);
}
.nav-button-selected {
    background-color: var(--secondary-color);
    color: var(--primary-color);
    transition: background-color 0.2s, color 0.2s; /* Add a smooth transition */
}

.main button {
    background-color: var(--primary-button--color);
    width: 196px;
    height: 45px;
    border-radius: 5px;
    border: 0;
    font-family: Inter;
    font-weight: 800;
    font-size: 20px;
    color: var(--primary-color);
    margin: 10px;
    cursor: pointer;
}

.header .header-wrapper .header-wrapper__links .text-button:hover {
    font-weight: lighter !important;
}

a:hover {
    font-weight: bolder !important;
}

.follow-us-div {
    font-family: Inter;
    color: var(--primary-color);

}

footer div {
    font-family: Inter;
    color: var(--primary-color);
}
.main button:hover {
    background-color: var(--tertiary-button-color);
}

.main button text {
    font-family: Inter;
    font-weight: bolder;
}

.header {
    text-align: center;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    color: var(--primary-color);
    left :0;

    position: fixed;
    top: 0;
    background-color: var(--primary-color);
    z-index: 1000;  /* Ensure it's above other elements */

}

.category-dropdown {
    width: 100%;
    height: 100%;
    padding: 0;
    margin: 0;
    border-bottom-left-radius: 8px;
    border-top-left-radius: 8px;
    text-indent: 15px;
    outline: none;
    border: none;
}

.search-icon {
    position: absolute;
    right: 0;
}

.input-div {
    width: 80%;
    position: relative;
    margin-right: 20px;
    height: 100%;
    display: flex;
    align-items: center;
    border-left: 1px solid black;
    padding-left: 20px;
}

.input-div input {
    width: 100%;
    height: 100%;
    padding: 0;
    margin: 0;
    border-width: 0;
    outline: none;
}

.input-div .search-icon {
    position: absolute;
    right: 0;
}

.header .header-wrapper {
    display: flex;
    justify-content: space-between;
    width: 100%;
    align-items: flex-start;
    padding-top: 20px;

}

.header-wrapper__links {
    display: flex;
    /*margin-top: 15px;*/
    gap: 16px;
    flex-direction: column;
    margin-right: 40px;
    align-items: end;
}

.header .header-wrapper .header-wrapper__links .text-button {
    color: var(--secondary-color);
    font-family: Inter;
    text-decoration: none;
    font-size: 18px;
    font-weight: 700;
}

.header .menu {
    /*padding-left: 10px;*/
    /*margin-left: 40px;*/
    display: flex;
    align-items: center;
}

.header-right {
    display: flex;
    gap: 20px;

}

.header-wrapper__input-wrapper {
    display: flex;
    border-radius: 8px;
    align-items: center;
    width: 450px;
    height: 40px;
    background-color: #FFFFFF;
    flex-direction: row;
    position: relative;
    outline: none;
    /*margin-right: 40px;*/
}

.header .header-bottom-wrapper {
    flex: 1 0 100%;
    display: flex;
    align-items: center;
    margin: 1rem -1rem -1rem -1rem;
    padding: 1rem;
    order: 2;
    justify-content: space-between;
    flex-wrap: wrap;
}

.header-wrapper__input-wrapper .dropdown-div {
    width: 35%;
    height: 100%;
    padding-right: 10px;
}

/* Style for the search bar container */
.search-bar {
    display: flex;
    align-items: center;
    /*background-color: #f0f0f0;*/
    padding: 10px;
}


.header .cart {
    font-size: 24px;
    margin-left: auto;
    cursor: pointer;
    position: relative;
    padding-right: 0
}

.cart-icon:hover {
    filter: brightness(50%) ;
}

.header .logo {
    margin-left: 26px
}


/* Footer Styles */
.footer {
    background-image: linear-gradient(0deg, var(--primary-color) 10%, var(--secondary-text-color) 100%);
    text-align: center;
    padding-top: 15px;
    width: 100%;
    position: fixed;

    bottom: 0;  /* Stick to the bottom of the viewport */
    left: 0;
    z-index: 1000;  /* Ensure it's above other elements */
}

.footer .footer-images {
    width: 100%;
    display: flex;
    height: auto;
}

.footer .footer-images img {
    padding-top: 10px;
    height: 22px;
    width: 100%;
    object-fit: cover;
    background-repeat: repeat;
}

.footer a {
    color: var(--primary-color);
    text-decoration: none;
    font-family: Inter;
    font-weight: normal;
    margin: 0 10px;
}
.image-hover-effect:hover {
    filter: invert(100%) hue-rotate(90deg);

}

.footer .follow-us {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 10px;
}

.follow-us-div {
    display: inline-flex;
    justify-content: space-evenly;
}

.footer .follow-us img {
    width: 20px;
    height: 20px;
    margin-left: 5px;
}

.footer .footer-links {
    display: flex;
    justify-content: space-between;
    margin-left: 80px;
    margin-right: 80px;
}



