.subtotal-section-checkout {
    display: flex;
    flex-direction: column;
    align-items: center;
    /*border: 1px solid var(--primary-color);*/
    border-radius: 10px;
    padding: 20px;
    width: 100%;
    min-width: 48vh;
    background: rgba(255, 255, 255, 0.50);

}
.subtotal-section-checkout p {
    margin: 4px;

}
.subtotal-section-checkout .subtotal-info-checkout {
    display: flex;
    width: 100%;
    align-items: flex-start;

}
.checkout-empty {
    font-family: Inter;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.checkout-cart-table-view {
    padding-top: 40vh;
    display: flex;
    flex-direction: row;
    align-content: space-around;
    /*width: 1100px;*/
    overflow-y: auto;
    padding-bottom: 20vh;
}
.checkout-cart-table {
    display: flex;
    flex-direction: column;
}
.checkout-cart-info {
    display: grid;
    grid-template-columns: repeat(2, 350px);
    row-gap: 2em;
    grid-gap: 0px;
    /*padding: 10px;*/
    margin: 0;

}

.checkout-display {
    margin-top: 80px;
    width: 300px;
    height: 250px;
    /*border: 1px solid black;*/
    border-radius: 10px;
}
.checkout-text {
    margin-top: 40px;
    margin-left: 50px;
    margin-bottom:20px;
    /*height: 150px;*/
    /*text-align: right;*/
    /*//  border: 1px solid black;*/
    /*  border-radius: 10px;*/
}
.checkout-page-body {
    display: flex;
    flex-direction: column;
    /*border: 1px solid black;*/
    border-radius: 10px;
    padding: 1em;
    gap: 40px;
    background: rgba(39,44,93, 0.70);

}
.checkout-cart-info-ul {
    display: contents;
}
.checkout-cart-info-ul > li {
    display: contents;
}
.checkout-table-heading {
    background-color: rgba(79, 203, 242, 0.32);
    color: black;
}
.checkout-table-heading > * {
    background-color: rgba(79, 203, 242, 0.32);
    padding: 0.5em;
}
.checkout-heading-book {
    grid-column: 1 / 3;
}
.checkout-heading-price {
    grid-column: 3 / 5;
    text-align: right;
}
.checkout-heading-subtotal {
    text-align: right;
    grid-column: -2 / -1;
}
/*.checkout-cart-book-image {*/
/*    width: 150px;*/
/*    height: 200px;*/
/*}*/
.subtotal-info-checkout {
    display: flex;
    flex-direction: column;
}
.checkout-cart-book-title {
    font-weight: bold;
    font-family: Inter;
}
.checkout-cart-book-info {
    padding-top: 15px;

    font-family: Inter;
    margin-right: 0;
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    justify-items: start;
    height: 150px;
}


.checkout-cart-book-item {

    margin-right: 0;


    flex-direction: column;
    flex-wrap: wrap;
    background: rgba(255, 255, 255, 0.50);
    width: 320px;
    height: 220px;
    border-radius: 10px;
    overflow: hidden;

    justify-content: flex-start;
    align-items: center;
    display: inline-flex;
    margin-left: 20px;
    margin-top: 20px;



}

.checkout-cart-info-img {
    width: 120px;
    height: 200px;
    padding-top: 15px;
}
.checkout-row-line {
    border-bottom: 1px solid black;
}
.checkout-num-button {
    background-color: transparent;
    border: 0px;
    font-family: Inter;
    align-items: center;
}
.checkout-narrow-rect {
    width: 75px;
    height: 100px;
}

.checkout-square {
    width: 100px;
    height: 100px;
}

.checkout-wide-rect {
    width: 125px;
    height: 100px;
}

.checkout-cart-book-price {
    padding-left: 1em;
    text-align: right;
}

.checkout-cart-book-quantity {
    display:flex;

}

.checkout-cart-book-subtotal {
    /*padding-left: 1em;*/
    padding-right: 1em;
}
/* Row separators in the table */

.checkout-btn-primary {
    background-color: var(--primary-button--color);
    width: 150px;
    height: 30px;
    border-radius: 5px;
    border: 0;
    font-family: Inter;
    font-weight: 800;
    font-size: 20px;
    color: var(--primary-color);
    margin: 10px;
    cursor: pointer;
}
.checkout-btn-primary:hover {
    background-color: var(--tertiary-button-color);
}
.checkout-btn-primary text {
    font-family: Inter;
    font-weight: bolder;
}
.checkout-btn-secondary {
    font-family: Inter;
    font-style: normal;
    height: 25px;
    width: 200px;
    font-size: 20px;
    margin-left: 30px;
    background-color: var(--primary-button--color);
    color: black;
    border: 1px solid #555;
    margin-top: 10px;
    border-radius: 40px;
}

.checkout-btn-ter {
    font-family: "Montserrat Alternates";
    font-style: normal;
    height: 20px;
    width: 100px;
    font-size: 15px;
    margin-left: 20px;
    color: #1510f5;
    background-color: white;
    border: none;
    margin-top: 10px;
    border-radius: 40px;
}
/* Increment/decrement buttons */

.checkout-icon-button {
    border: none;
    cursor: pointer;
    background-color: transparent;
    color: #000000;
    padding: 0;
}

.checkout-inc-button {
    font-size: 1.125rem;
    color: var(--primary-color);
    margin-right: 0.25em;
}

.checkout-btn-ter:hover {
    color: var(--primary-color);
}

.checkout-dec-button {
    font-size: 1.125rem;
    color: #ccc;
}
.checkout-dec-button:hover {
    color: #aaa;
}

/* Chevron buttons */

.checkout-dec-arrow-button,
.checkout-inc-arrow-button {
    font-size: 1rem;
    color: var(--primary-color);
}

.checkout-dec-arrow-button:hover,
.checkout-inc-arrow-button:hover {
    color: var(--primary-color);
}



.checkout-select {
    background-color: var(--primary-color);
    color: black;
    border: 2px solid var(--primary-color);
    border-radius: 3px;

}
form {
    display: flex;
    width: 100%;

    background: rgba(255, 255, 255, 0.50);
    flex-direction: column;
    border: 1px solid black;
    border-radius: 10px;
    padding: 20px;

}
form > div > label {
    width: 70px;
    font-family: Inter;
    font-weight: bold;

}
form > div {
    display: flex;
    justify-content: space-between;
    margin-top: 0.5em;
}

form > div:first-child {
    margin-top: 0;
}

.checkout-row-line {
    border-bottom: 2px solid black; /* You can adjust the color as needed */
}
form > div > input {
    width: 100%;
}
form > div > input,
form > div > select {
    background-color: white;
    margin-left: 0.5em;
    border-radius: 4px;
    border-width: 1px;
    height: 25px;
}
form  .error {
    color: red;
    text-align: right;
    font-size: 0.7em;
    color: #cc0033;
    display: inline-block;
    line-height: 15px;
    margin: 5px 0 0;
}
.error-messages {
    color: red;
    display: flex;
    flex-direction: column;
    font-family: Inter;
    font-style: italic;
    align-items: flex-start;
    font-size: 20px;
}
.checkoutStatusBox {
    /*margin: 1em;*/
    /*padding: 1em;*/
    text-align: center;
    font-weight: bold;
    font-family: Inter;

}
.checkout-view {
    dsiplay: flex;
    flex-direction: column;
    border: 1px solid black;
    border-radius: 10px;
}

